  //           2
  //         )
  //       });
  //     }
  //   });

  //console.table(imoex);
  //console.table(selectedstock);

  let scatterdata =[];
  let arrayOfarrays=[["Date", "IMOEX", company]]; 

  selectedstock.length > 0 &&
    selectedstock.forEach(item => {
      let found = imoex.find(function(element) {
        return element.DATE === item.DATE;
      });
      let imoexPeriodReturn = found.PERIODRETURN
      console.log(
        imoexPeriodReturn,
        item.PERIODRETURN
      );
      scatterdata.push({
        horizontal: imoexPeriodReturn, vertical: item.PERIODRETURN
      })
      arrayOfarrays.push([item.DATE, imoexPeriodReturn, item.PERIODRETURN ])

      // if (found.hasOwnProperty(PERIODRETURN) && item.hasOwnProperty(PERIODRETURN)) {
      //   //     return { horizontal: found.PERIODRETURN, vertical: item.PERIODRETURN };
      //   console.log("Common date " + element.DATE);
      // }
    });

  // let scatterdata = selectedstock.map((item, index) => {
  //   let found = imoex.find(function(element) {
  //     return element.DATE === item.DATE;
  //   });

