import React, { useEffect, useState, useRef } from "react";
import * as d3 from "d3";
import regression from "regression";

export default props => {
  const [dataset, setDataset] = useState(props.data);
  const ref = useRef();

  useEffect(() => {

    let dataForRegression=props.data.map((item, index)=> [item.horizontal, item.vertical]);
    console.log(dataForRegression);
    let result = regression.linear(dataForRegression); //[[0, 1], [32, 67], [12, 79]]
  //  console.log(result.equation[0]);
  //  console.log(result.equation[1]);
    console.log(result.string);
    console.log(result.r2);

    let svg = d3.select(ref.current);

    var margin = { top: 10, right: 10, bottom: 50, left: 50 };
    var width = +svg.attr("width") - margin.left - margin.right;
    var height = +svg.attr("height") - margin.top - margin.bottom;

    var g = svg
      .append("g")
      .attr("transform", "translate(" + margin.left + "," + margin.top + ")");

    // g.append("text")
    //   .attr("x", 0)
    //   .attr("y", 5)
    //   .attr("text", "Text");

    svg.append("text")
    .attr("x", 5)
    .attr("y", 20)
    .attr("class", "label")
    .text(result.string);

    // Scales
    var x = d3.scaleLinear().range([0, width]);
    var y = d3.scaleLinear().range([height, 0]);

    // Axes
    var xAxisCall = d3.axisBottom(x);
    var xAxis = g
      .append("g")
      .attr("class", "x-axis")
      .attr("transform", "translate(" + 0 + "," + height / 2 + ")");

    var yAxisCall = d3.axisLeft(y);
    var yAxis = g
      .append("g")
      .attr("class", "y-axis")
      .attr("transform", "translate(" + width / 2 + "," + 0 + ")");

    // Labels
    xAxis
      .append("text")
      .attr("class", "axis-title")
      .attr("transform", "translate(" + width + ", 0)")
      .attr("y", -6)
      .text("Grade Point Average");
    yAxis
      .append("text")
      .attr("class", "axis-title")
      .attr("transform", "rotate(-90)")
      .attr("y", 16)
      .text("Height / Centimeters");

    var flag = true;

    // Run this code every second...
    d3.interval(function() {
      // Flick between our two data arrays
      //let data = flag ? data0 : data1;
      let data = generateDataset();
      // Update our chart with new data
      update(data);

      // Update our flag variable
      flag = !flag;
    }, 30000);

    // Run for the first time
    update(props.data);

    function update(data) {
      // Standard transition for our visualization
      var t = d3.transition().duration(750);

      // Update our scales
      x.domain([-30, 30]);
      y.domain([-30, 30]);

      // Update our axes
      xAxis.transition(t).call(xAxisCall);
      yAxis.transition(t).call(yAxisCall);

      // Update text
      let legend = g.selectAll("text");

      legend
      .exit()
      .transition(t)
      .attr("fill-opacity", 0.1)
      .remove();

      // legend
      //   .transition(t)
      //   .attr("x", 50)
      //   .attr("y", 50);

      // legend
      //   .enter()
      //   .append("text")
      //   .attr("x", 50)
      //   .attr("y", 50)
      //   .attr("text", "Легенда")
      //   .attr("fill", "grey")
      //   .attr("fill-opacity", 0.1)
      //   .transition(t)
      //   .attr("fill-opacity", 1)
      //   .attr("y", 50);

      // Update our circles
      var circles = g.selectAll("circle").data(data);

      circles
        .exit()
        .transition(t)
        .attr("fill-opacity", 0.1)
        .attr("cy", y(0))
        .remove();

      circles
        .transition(t)
        .attr("cx", function(d) {
          return x(d.horizontal);
        })
        .attr("cy", function(d) {
          return y(d.vertical);
        });

      circles
        .enter()
        .append("circle")
        .attr("cx", function(d) {
          return x(d.horizontal);
        })
        .attr("cy", y(0))
        .attr("r", 3)
        .attr("fill", "grey")
        .attr("fill-opacity", 0.1)
        .transition(t)
        .attr("fill-opacity", 1)
        .attr("cy", function(d) {
          return y(d.vertical);
        });
    }
  }, [dataset]);

  return (
    <svg
      width="700"
      height="600"
      //    viewBox="0 0 800 800"
      ref={ref}
    />
  );
};

const generateDataset = () =>
  Array(100)
    .fill(0)
    .map(() => ({
      horizontal: Math.random() * 60 - 30,
      vertical: Math.random() * 60 - 30
    }));
