import React from "react"
import { useStaticQuery, graphql } from "gatsby"

const QuotesHook = () => {
  const data = useStaticQuery(graphql`
  query MyQuery {
    allDataCsv {
      totalCount
      edges {
        node {
        id
        TICKER
        PER
        DATE
        TIME
        OPEN
        HIGH
        LOW
        CLOSE
        VOL
        }
       }
     }
   }    
  `)
  return data.allDataCsv
}

export default QuotesHook;

